import Vue from "vue";
import axios from "axios";
import Vuex from "vuex";
Vue.use(Vuex);
import { saveToLocal, removeFromLocal, getFromLocal } from "./utils/helper";
import router from "./router";

const success = getFromLocal("success")
const naira = getFromLocal("naira")
const kano = getFromLocal("kano")
const naija_gold = getFromLocal("naija_gold")
const rich = getFromLocal("rich")
const lg = getFromLocal("lg")
const msp = getFromLocal("msp")
const midweek = getFromLocal("midweek")
const bonanza = getFromLocal("bonanza")
const fortune = getFromLocal("fortune")
const national = getFromLocal("national")
const cash = getFromLocal("cash")
const winna = getFromLocal("winna")
const millim = getFromLocal("millim")
const user = getFromLocal("currentUser")
const userDetails = getFromLocal("userDetails")
const userBalance = getFromLocal("userBalance")
const walletAddress = getFromLocal("walletAddress")

export default new Vuex.Store({
    state: {
        currentUser: user,
        loginStatus: !!user,
        loginError: '',
        success: success || [],
        naira: naira || [],
        kano: kano || [],
        naija_gold: naija_gold || [],
        rich: rich || [],
        lg: lg || [],
        bonanza: bonanza || [],
        fortune: fortune || [],
        midweek: midweek || [],
        national: national || [],
        msp: msp || [],
        cash: cash || [],
        winna: winna || [],
        millim: millim || [],
        userDetails: userDetails || null,
        userBalance: userBalance || null,
        disableCategory: false,
        currentGameCategory: '',
        schedule: [],
        eTranzactInit: {},
        interswitchInit: {},
        transactionStatus: false,
        uPaymentInit: '',
        walletAddress: walletAddress || '',
    },
    getters: {
        getNewSchedule: (state) => {
            return state.schedule;
        },
        getETranzactInit: (state) => {
            return state.eTranzactInit;
        },
        getInterswitchInit: (state) => {
            return state.interswitchInit;
        },
        getUPaymentInit: (state) => {
            return state.uPaymentInit;
        },
        getTransactionStatus: (state) => {
            return state.transactionStatus;
        },
        getWalletAddress: (state) => {
            return state.walletAddress;
        },
        commafy: (state) => (x) => {
            return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
    },
    mutations: {
        setSuccess(state, { game, category }) {
            state.success.push(game);
            saveToLocal(state, category);
        },
        setNaira(state, { game, category }) {
            state.naira.push(game);
            saveToLocal(state, category);
        },
        setKano(state, { game, category }) {
            state.kano.push(game);
            saveToLocal(state, category);
        },
        setNaijaGold(state, { game, category }) {
            state.naija_gold.push(game);
            saveToLocal(state, category);
        },
        setMidWeek(state, { game, category }) {
            state.midweek.push(game);
            saveToLocal(state, category);
        },
        setRich(state, { game, category }) {
            state.rich.push(game);
            saveToLocal(state, category);
        }, 
        setLg(state, { game, category }) {
            state.lg.push(game);
            saveToLocal(state, category);
        },
        setBonanza(state, { game, category }) {
            state.bonanza.push(game);
            saveToLocal(state, category);
        },
        setFortune(state, { game, category }) {
            state.fortune.push(game);
            saveToLocal(state, category);
        },
        setMsp(state, { game, category }) {
            state.msp.push(game);
            saveToLocal(state, category);
        },
        setNational(state, { game, category }) {
            state.national.push(game);
            saveToLocal(state, category);
        },
        setCash(state, { game, category }) {
            state.cash.push(game);
            saveToLocal(state, category);
        },
        setWinna(state, { game, category }) {
            state.winna.push(game);
            saveToLocal(state, category);
        },
        setMillim(state, { game, category }) {
            state.millim.push(game);
            saveToLocal(state, category);
        },
        removeGame(state, { category, index }) {
            state[category].splice(index, 1)
            saveToLocal(state, category);
        },
        loginSuccess(state, payload) {
            state.loginStatus = true
            state.currentUser = Object.assign({}, { token: payload.token }, { user: { id: payload.response.data.id, name: payload.response.data.real_name, telephone: payload.response.data.telephone } });
            saveToLocal(state, "currentUser")
            router.push('/dashboard')
        },
        loginFaill(state, error) {
            state.loginError = error.mssg
        },
        logout(state) {
            state.currentUser = null
            state.loginStatus = false
            removeFromLocal("currentUser")
            router.push('/')
        },
        setUserDetails(state, payload) {
            state.userDetails = payload
            saveToLocal(state, "userDetails")
        },
        setWalletAddress(state, payload) {
            console.log(payload);
            state.walletAddress = payload;
            saveToLocal(state, "walletAddress");
        }, 

        setUserBalance(state, payload) {
            state.userBalance = payload.result_desc
            saveToLocal(state, "userBalance")
        },
        updateDisabledState(state, payload) {
            state.disableCategory = payload.boolean
            state.currentGameCategory = payload.category
        },
        setSchedule(state, payload) {
            state.schedule = payload.statusCode
        },
        setETranzactInit(state, payload) {
            state.eTranzactInit = payload
        },
        setTransactionStatus(state, payload) {
            console.log('payload:', payload)
            state.transactionStatus = payload;
        },
        setInterswitchInit(state, payload) {
            console.log('payload:', payload)
            state.interswitchInit = payload;
        },
        setUPaymentInit(state, payload) {
            console.log('payload:', payload)
            state.uPaymentInit = payload.id;
        },
        clearTicket(state, category) {
            console.log(category, 'cool');
            state[category] = []
            //removeFromLocal(category);
            localStorage.removeItem(category);

        }
    },
    actions: {
        async login({ commit }, params) {
            axios.post("https://middlewarez.herokuapp.com/api/v1/auth/game/user?message=games&auth=55904993399349495bb6fdd&as=user&task=authenticate", {
                    ...params
                })
                .then(({ data }) => {
                    if (data.ok) {
                        commit("loginSuccess", data)
                    } else {
                        commit("loginFaill", data)
                    }
                })
        },
        async register({ commit, dispatch }, params) {
            axios.post("https://middlewarez.herokuapp.com/api/v1/auth/game/user?message=games&auth=55904993399349495bb6fdd&as=user&task=register", {
                    ...params
                })
                .then(({ data }) => {
                    dispatch('login', { username: data.response.result_desc.username, password: data.response.result_desc.password })

                })
        },
        async getuserDetails({ commit, state }) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/game/account?message=games&auth=55904993399349495bb6fdd&as=account&task=userinfo", {
                    "telephone": state.currentUser.user.telephone
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    commit("setUserDetails", data.response)
                })
        },
        async getBalance({ commit, state }) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/game/account?message=games&auth=55904993399349495bb6fdd&as=account&task=balance", {
                    "username": state.userDetails.username
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    commit("setUserBalance", data.response)
                })
        },
        async updateAccountNumber({ commit, state }, account_number) {
            await axios.put("https://middlewarez.herokuapp.com/api/v1/activites/account/update?auth=55904993399349495bb6fdd", {
                    "accountNumber": account_number
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {

                })
        },
        async transferFund({ commit, state }, payload) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/account/transfer?auth=55904993399349495bb6fdd", {
                    "senderNumber": state.userDetails.telephone,
                    "receiverNumber": payload.receiverNumber,
                    "amount": payload.amount,
                    "currency": "NGN"
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    console.log(data)
                })
        },
        async initiatePayment({ commit, state }, payload) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/account/etransact/initiate?auth=6399", {
                    "telephone": state.userDetails.telephone,
                    "email": state.userDetails.email,
                    "amount": payload.depositAmount,
                    "fullName": `${state.userDetails.firstName} ${state.userDetails.lastName}`,
                    "userName": state.userDetails.username
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    console.log(data)
                    commit("setETranzactInit", data)
                })
        },
        
        async completePayment({ commit, state }, payload) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/account/etransact/complete?auth=6399", {
                    ...payload,
                    username: state.userDetails.username
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    console.log(data)
                    commit("setTransactionStatus", true)
                }).catch(e => {
                    console.log(e, 'error');
                    commit("setTransactionStatus", false)
                })
        },
        async initiateITSW({ commit, state }, payload) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/returnurl/interswitch/initiate?auth=6399", {
                    "amount": payload.depositAmount,
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    console.log(data)
                    commit("setInterswitchInit", data)
                })
        },
        async initiateUPayment({ commit, state }, payload) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/account/unified/initiate?auth=6399", {
                    "amount": payload.depositAmount,
                }, {
                    headers: {
                        'Authorization': `${state.currentUser.token}`
                    }
                })
                .then(({ data }) => {
                    console.log(data)
                    commit("setUPaymentInit", data)
                })
        },
        async getSchedule({ commit }) {
            await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/game/schedule?as=game&task=schedule&auth=55904993399349495bb6fdd")
                .then(({ data }) => {
                    commit('setSchedule', data)
                })
        },
        async placeBet({ commit, state }, payload) {
            var body = {
                "message": "lottobet",
                "data": {
                    "username": state.userDetails.username,
                    "type": "lottery",
                    "selections": []
                }
            }
            var games = JSON.parse(JSON.stringify(payload));

            games.forEach(load => {
               // if (load.selection) {
                //     load.selection = load.isMachine ? `(+M)${load.selection.toString()}` : load.selection.toString();
                // }
                // if (load.type == 'Banker Bet') {
                //     // load.selection = load.isMachine ? `(+M)A:${load.selection1.toString()}-B:${load.selection2.toString()}` : `A:${load.selection1.toString()}-B:${load.selection2.toString()}`
                //     delete load.selection1
                //     delete load.selection2
                // }
                delete load.type
                delete load.isMachine

            })

            body.data.selections = games;
            // console.log(body)
            return await axios.post("https://middlewarez.herokuapp.com/api/v1/activites/game/bet?as=game&task=bet&auth=55904993399349495bb6fdd&device=web", {

                    ...body
                })
                // .then(({ data }) => {
                //     router.push('/dashboard')

                //     return data
                // })
        },
    },
})

