import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
    linkExactActiveClass: "active",
    mode: 'history',
    routes: [{
            path: '/',
            component: () =>
                import ('@/layouts/Default.vue'),
            children: [{
                    path: '',
                    name: 'landing',
                    component: () =>
                        import ('@/views/Landing.vue')
                },
                {
                    path: 'not-found',
                    name: 'not-found',
                    component: () =>
                        import ('@/views/NotFound.vue')
                },
                {
                    path: 'about',
                    name: 'about',
                    component: () =>
                        import ('@/views/About.vue')
                },
                {
                    path: 'terms',
                    name: 'terms',
                    component: () =>
                        import ('@/views/Term&condition.vue')
                },
                {
                    path: 'policy',
                    name: 'policy',
                    component: () =>
                        import ('@/views/Privacy.vue')
                },
                {
                    path: 'contact',
                    name: 'contact',
                    component: () =>
                        import ('@/views/Contact.vue')
                },
                {
                    path: 'forgotpassword',
                    name: 'forgotpassword',
                    component: () =>
                        import ('@/views/Forgotpassword.vue')
                },
                {
                    path: 'media',
                    name: 'media',
                    component: () =>
                        import ('@/views/Media.vue')
                },
                {
                    path: 'result',
                    name: 'result',
                    component: () =>
                        import ('@/views/Result.vue')
                },
                {
                    path: 'login',
                    name: 'login',
                    component: () =>
                        import ('@/views/Login.vue')
                },
                {
                    path: 'register',
                    name: 'register',
                    component: () =>
                        import ('@/views/register.vue')
                },


            ]
        },
        {
            path: "/play",
            component: () =>
                import ('@/layouts/DefaultWithSideNav.vue'),
            meta: {
                requiresAuth: true
            },
            children: [
                { path: '', redirect: { name: 'dashboard' } },
                {
                    path: ':category/1-5-direct',
                    name: 'direct',
                    component: () =>
                        import ('@/views/games/Direct.vue')
                },
                {
                    path: ':category/+2-5-perm',
                    name: 'perm',
                    component: () =>
                        import ('@/views/games/Perm.vue')
                },
                {
                    path: ':category/banker',
                    name: 'banker',
                    component: () =>
                        import ('@/views/games/Banker.vue')
                },
                {
                    path: ':category/1-all',
                    name: 'one-all',
                    component: () =>
                        import ('@/views/games/1-All.vue')
                },
            ]
        },
        {
            path: "/dashboard",
            component: () =>
                import ('@/layouts/DefaultWithSideNav.vue'),
            meta: {
                requiresAuth: true
            },
            children: [{
                    path: '',
                    name: 'dashboard',
                    component: () =>
                        import ('@/views/dashboard/Dashboard.vue')
                },
                {
                    path: 'results',
                    name: 'dashboard-result',
                    component: () =>
                        import ('@/views/dashboard/DashboardResult.vue')
                },
                {
                    path: "/playgame",
                    component: () =>
                        import ('@/views/Play.vue'),
                    meta: {
                        requiresAuth: true
                    },
                },



            ]
        },


        {
            path: '/wallet',
            component: () =>
                import ('@/layouts/WalletLayout.vue'),
            meta: {
                requiresAuth: true
            },
            children: [{
                    path: 'transaction',
                    name: 'transaction-history',
                    component: () =>
                        import ('@/views/wallet/TransactionHistory.vue')
                },
                {
                    path: 'mainwallet',
                    name: 'main',
                    component: () =>
                        import ('@/views/wallet/FundWalletMain.vue')
                },
                {
                    path: 'deposit',
                    name: 'deposit',
                    component: () =>
                        import ('@/views/Deposit.vue')
                },
                {
                    path: 'transfer-funds',
                    name: 'transfer-funds',
                    component: () =>
                        import ('@/views/wallet/TransferFunds.vue')
                },
                {
                    path: 'widthdraw-funds',
                    name: 'widthdraw-funds',
                    component: () =>
                        import ('@/views/wallet/WidthdrawFunds.vue')
                },
                {
                    path: 'fund-wallet',
                    name: 'fund-wallet',
                    component: () =>
                        import ('@/views/wallet/FundWallet.vue')
                },
                {
                    path: 'account-details',
                    name: 'account-details',
                    component: () =>
                        import ('@/views/wallet/BankAccountDetails.vue')
                },
            ]
        },

        {
            path: '/resultv',
            component: () =>
                import ('@/layouts/ResultLayout.vue'),

            children: [{
                    path: '',
                    name: 'current-result',
                    component: () =>
                        import ('@/views/result/main.vue')
                }, {
                    path: '/success',
                    name: 'success',
                    component: () =>
                        import ('@/views/result/success.vue')
                },
                {
                    path: '/cash',
                    name: 'cash',
                    component: () =>
                        import ('@/views/result/cash.vue')
                },
                {
                    path: '/msp',
                    name: 'msp',
                    component: () =>
                        import ('@/views/result/msp.vue')
                },
                {
                    path: '/kano',
                    name: 'kano',
                    component: () =>
                        import ('@/views/result/kano.vue')
                },
                {
                    path: '/lg',
                    name: 'lg',
                    component: () =>
                        import ('@/views/result/lg.vue')
                },
                {
                    path: '/million',
                    name: 'million',
                    component: () =>
                        import ('@/views/result/million.vue')
                },
                {
                    path: '/naija_gold',
                    name: 'naija_gold',
                    component: () =>
                        import ('@/views/result/naija_gold.vue')
                },
                {
                    path: '/naira',
                    name: 'naira',
                    component: () =>
                        import ('@/views/result/naira.vue')
                },
                {
                    path: '/rich',
                    name: 'rich',
                    component: () =>
                        import ('@/views/result/rich.vue')
                },
                {
                    path: '/winna',
                    name: 'winna',
                    component: () =>
                        import ('@/views/result/winna.vue')
                },
            ]
        },
        {
            path: "*",
            component: () =>
                import ('@/views/NotFound.vue'),
        },


    ]
});